"use client";

import { useState } from "react";
import { Search } from "lucide-react";
import { useRouter } from "next/navigation";

export default function Hero() {
  const router = useRouter();
  const [searchTerm, setSearchTerm] = useState("");
  const [filters, setFilters] = useState({
    minPrice: "",
    maxPrice: "",
    type: "",
    bedrooms: "",
  });

  const handleSearch = (e) => {
    e.preventDefault();
    const params = new URLSearchParams();

    if (searchTerm) params.set("location", searchTerm);
    if (filters.minPrice) params.set("minPrice", filters.minPrice);
    if (filters.maxPrice) params.set("maxPrice", filters.maxPrice);
    if (filters.type) params.set("type", filters.type);
    if (filters.bedrooms) params.set("bedrooms", filters.bedrooms);

    router.push(`/properties?${params.toString()}`);
  };

  return (
    <div className="relative bg-gradient-to-r from-primary-600 to-primary-800 rounded-2xl overflow-hidden mb-12">
      <div className="absolute inset-0 bg-black/20" />
      <div className="relative px-8 py-12 md:py-16">
        <div className="max-w-4xl mx-auto text-center">
          <h1 className="text-4xl md:text-5xl font-bold text-white mb-4">
            Find Your Perfect Property
          </h1>
          <p className="text-xl text-primary-100 mb-8">
            Discover amazing properties for rent or sale across the country
          </p>

          {/* Search Bar */}
          <div className="bg-white rounded-xl shadow-xl p-4">
            <form onSubmit={handleSearch} className="space-y-4">
              <div className="flex items-center">
                <Search className="w-5 h-5 text-gray-400 ml-3" />
                <input
                  type="text"
                  placeholder="Search by location, city, or address..."
                  className="w-full px-4 py-3 text-gray-900 placeholder-gray-500 focus:outline-none"
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                />
                <button type="submit" className="btn-primary px-6 py-3 ml-2">
                  Search
                </button>
              </div>

              {/* Filters */}
              <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
                <div>
                  <select
                    className="input-field"
                    value={filters.type}
                    onChange={(e) =>
                      setFilters({ ...filters, type: e.target.value })
                    }
                  >
                    <option value="">All Types</option>
                    <option value="apartment">Apartment</option>
                    <option value="house">House</option>
                    <option value="condo">Condo</option>
                    <option value="villa">Villa</option>
                  </select>
                </div>

                <div>
                  <select
                    className="input-field"
                    value={filters.bedrooms}
                    onChange={(e) =>
                      setFilters({ ...filters, bedrooms: e.target.value })
                    }
                  >
                    <option value="">Any Bedrooms</option>
                    <option value="1">1+ Bedrooms</option>
                    <option value="2">2+ Bedrooms</option>
                    <option value="3">3+ Bedrooms</option>
                    <option value="4">4+ Bedrooms</option>
                  </select>
                </div>

                <div>
                  <input
                    type="number"
                    placeholder="Min Price"
                    className="input-field"
                    value={filters.minPrice}
                    onChange={(e) =>
                      setFilters({ ...filters, minPrice: e.target.value })
                    }
                  />
                </div>

                <div>
                  <input
                    type="number"
                    placeholder="Max Price"
                    className="input-field"
                    value={filters.maxPrice}
                    onChange={(e) =>
                      setFilters({ ...filters, maxPrice: e.target.value })
                    }
                  />
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}
